----------------------------------------------------------------------------- */
body {
  position: relative;
  margin-top: calc(70px + 54px);
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

