/* -----------------------------------------------------------------------------
  Component: Breadcrumbs

  Style for the breadcrumbs present in the content area.
----------------------------------------------------------------------------- */
.breadcrumb-wrapper {
  position: fixed;
  top: 77px;
  z-index: 99;
  display: flex;
  flex-direction: row;
  width: 100%;
  min-height: 60px;
  padding: 5px 15px 5px 30px;
}

@media (max-width: 575px) {
  .breadcrumb-wrapper {
    position: static;
    overflow: auto;
  }
}

@media (max-width: 1199px) {
  .breadcrumb-wrapper {
    top: calc(70px + 54px);
    align-items: center;
    padding-left: 15px;
  }
}

@media (min-width: 1200px) {
  .breadcrumb-wrapper {
    align-items: flex-end;
    max-height: 90px;
  }
}

@media (min-width: 1200px) and (max-width: 1599px) {
  .breadcrumb-wrapper {
    width: calc(100% - 320px - 240px);
  }
}

@media (min-width: 1600px) {
  .breadcrumb-wrapper {
    width: calc(100% - 385px - 290px);
  }
}

#breadcrumbs {
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
}

@media (max-width: 575px) {
  #breadcrumbs {
    display: block;
  }
}

@media (max-width: 575px) {
  #breadcrumbs {
    font-size: 14px;
  }
}

@media (min-width: 1200px) and (max-width: 1299px) {
  #breadcrumbs {
    font-size: 14px;
  }
}

#breadcrumbs li {
  display: inline;
}

.breadcrumb-separator::after {
  display: inline-block;
  margin: 0 7px;
  font-size: 1.1rem;
  content: "/";
}

