/* -----------------------------------------------------------------------------
  Component: Code-blocks

  Style for the code-blocks and adjustment of the pygments.
----------------------------------------------------------------------------- */
main code,
main kbd,
main pre,
main samp {
  font-family: "Fira Mono", monospace;
}

.literal-block-wrapper.docutils.container {
  max-width: 100%;
  padding-right: 0;
  padding-left: 0;
}

.literal-block-wrapper.docutils.container .code-block-caption {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 48px;
  padding: 5px 10px;
  margin-top: 1rem;
  border-radius: 10px 10px 0 0;
}

.literal-block-wrapper.docutils.container .code-block-caption .headerlink {
  line-height: 1;
  text-decoration: none;
}

.literal-block-wrapper.docutils.container .code-block-caption .headerlink::after {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  font: 400 .95rem/1 FontAwesome, sans-serif;
  text-align: right;
  vertical-align: middle;
  content: "\f0c1";
  opacity: 0;
  transition: opacity ease-in .1s;
}

.literal-block-wrapper.docutils.container .code-block-caption:hover .headerlink::after {
  opacity: 1;
  transition: opacity ease-in .1s;
}

.literal-block-wrapper.docutils.container tbody tr td {
  padding: 0;
  border: none;
}

.literal-block-wrapper.docutils.container [class*="highlight-"] {
  margin-top: 0;
}

[class*="highlight-"] {
  position: relative;
  max-width: 100%;
  margin-top: 1rem;
  margin-bottom: 1rem;
  overflow-y: hidden;
  border: 1px solid;
  transition: max-height .2s;
}

[class*="highlight-"].output {
  max-height: 100vh;
  border-radius: 10px;
}

[class*="highlight-"].output .output-title {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  font-weight: 600;
  cursor: pointer;
  border-radius: 10px 10px 0 0;
}

[class*="highlight-"].output .output-title::before {
  width: 40px;
  height: 40px;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
  background-size: 18px 18px;
  transition: transform .1s;
}

[class*="highlight-"].output .linenodiv {
  top: 60px;
}

[class*="highlight-"].output.collapsed {
  max-height: 40px;
  transition: max-height .2s;
}

[class*="highlight-"].output.collapsed .output-title {
  border-radius: 10px;
  transition: border-radius .05s .2s;
}

[class*="highlight-"].output.collapsed .output-title::before {
  transition: transform .1s;
  transform: rotateZ(-90deg);
}

[class*="highlight-"].output.expanded {
  max-height: unset;
}

[class*="highlight-"] .highlighttable {
  width: 100%;
  margin-top: 0;
  margin-bottom: 0;
  table-layout: fixed;
}

[class*="highlight-"] .highlighttable .highlight {
  position: unset;
  max-width: 100%;
  padding-right: 0;
